<template>
  <div id="app">
    <div class="mdl-layout mdl-js-layout">
      <header class="mdl-layout__header">
        <div class="mdl-layout__header-row">
          <span class="mdl-layout-title">CropChat</span>
        </div>
      </header>
      <div class="mdl-layout__drawer">
        <span class="mdl-layout-title">CropChat</span>
        <nav class="mdl-navigation">
          <router-link to="/" @click="hideMenu" class="mdl-navigation__link">Home</router-link>
          <router-link to="/post" class="mdl-navigation__link">Post a picture</router-link>
        </nav>
      </div>
      <main class="mdl-layout__content">
        <div class="page-content">
          <router-view></router-view>
        </div>
      </main>
    </div>

  </div>
</template>

<script>
require('material-design-lite')
export default {
  name: 'app',
  methods: {
    hideMenu () {
      document.querySelector('.mal-layout__drawer')
        .classList.remove('is-visible')
      document.querySelector('.mdl-layout__obfuscator')
        .classList.remove('is-visible')
    }
  }
}
</script>

<style>
@import url('https://code.getmdl.io/1.2.1/material.blue-red.min.css');
@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: local('Material Icons'),
  local('MaterialIcons-Regular'),
  url(https://fonts.gstatic.com/s/materialicons/v22/2fcrYFNaTjcS6g4U3t-Y5UEw0lE80llgEseQY3FEmqw.woff2)
  format('woff2');
}
.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  -webkit-font-feature-settings: 'liga';
  -webkit-font-smoothing: antialiased;
}
.mdl-navigation__link {
  background-color: #fff;
}
</style>



